  const startPayment = async ({ setError, setTxs, ether, addr, chain }) => {
    // setQrdata('');

    try {
      if (!window.ethereum)
        throw new Error("No crypto wallet found. Please install it.");
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      // await ethers.utils.getAddress(addr);
      console.log("---------", addr)
      setSendAddress(addr);
      console.log('right chain:', connected_chain, chain)
      if ((chain === 0 && connected_chain === '0x1') || (chain === 1 && connected_chain === '0x38')) {

        //when user transfer usdc
        if (chain === 0 && (rechargeamount >= ether * Number(currentfee) / 100)) {
          let contract = new ethers.Contract(
            usdc_address,
            usdc_abi,
            signer
          )

          // How many tokens?
          let numberOfTokens = ethers.utils.parseUnits(ether, 18)
          console.log(`numberOfTokens: ${numberOfTokens}`)

          // const { getGasPrice, getGasLimit } = await ethersGasUtils(provider, contract, [addr, numberOfTokens]);

          // Send tokens
          const sendNativeToken = await contract.transfer(addr, numberOfTokens).then((transferResult) => {
            console.dir(transferResult);

            setAmount(ether);
            setToken("USDC");
            setTxs([transferResult]);


          });
          console.log("NativeTOken", sendNativeToken)
          setLoading(true);
          await sendNativeToken;
          setLoading(false);

        }
        //when user transfer busd
        else if (chain === 1 && rechargeamount >= (ether * Number(currentfee) / 100)) {
          console.log("feeefefefe", ether * Number(currentfee) / 100)

          let contract = new ethers.Contract(
            busd_address,
            busd_abi,
            signer
          )

          // How many tokens?
          let numberOfTokens = ethers.utils.parseUnits(ether, 18)
          console.log(`numberOfTokens: ${numberOfTokens}`)

          // Send tokens
          const sendNativeToken = await contract.transfer(addr, numberOfTokens).then((transferResult) => {
            console.dir(transferResult);

            setAmount(ether);
            setToken("BUSD");
            setTxs([transferResult]);


          });
          console.log("NativeTOken", sendNativeToken)
          setLoading(true);
          await sendNativeToken;
          setLoading(false);



          //when user no recharge
        } else {
          console.log("feefeefee", ether * Number(fee) / 100)
          alert("you have to recharge! for your transaction")
        }




      }
      else {
        if (chain * chain === 1) {
          console.log("BNB chain")

          await web3Provider.provider.request({
            method: "wallet_addEthereumChain",
            params: [chains[chain * chain]]
          }).then(() => setChain('0x38'))
        }
        else {
          console.log("ethereum chain")
          // await window.ethereum.request({
          //   method: "wallet_switchEthereumChain",
          //   params: [{ chainId: `0x1` }],
          // }).then(() => setChain('0x1'))

          await web3Provider.provider.request({
            method: "wallet_switchEthereumChain",
            params: [{ chainId: '0x1' }],
          }).then(() => setChain('0x1'))
        }

        console.log("startpayment function", connected_chain)
      }


    } catch (err) {
      console.log('err', err)
      if (err.data !== undefined && err.data.message !== undefined) setError(err.data.message);
      else setError(err.message);
    }
  };


